.ql-tooltip .ql-remove:before.disabled,
.ql-tooltip .ql-remove:before:disabled {
  background-color: #fff;
  border-color: #fff;
  color: #283e59;
}
.ql-tooltip.ql-editing .ql-remove:before {
  display: none;
}
.ql-editor blockquote {
  font-size: 1.17188rem;
  margin-bottom: 1.5rem;
}
.ql-editor img {
  height: auto;
  max-width: 100%;
}
.list-pagination > li + li {
